.app-wrapper {
  max-width: 70%;
  margin: 0px auto;
  display: grid;
  gap: 10px;
  grid-template-columns: 2fr 10fr;
  grid-template-rows: 60px 1fr;
  grid-template-areas:
    "h h"
    "side-bar cont";
}

.app-wrapper-content{
  grid-area: cont;
 
}

.image-container{
    width: 100%;
    height: 200px;
}
.image-container img{
    object-fit: cover;
    width: 100%;
    height: 100%;
    object-position: center;
}